import {detail} from "../../APIs/index.js"
export default {
	data() {
		return {
			id:0,
			detail:{}
		}
	},
	onLoad(option) {
		this.id = option.id
		detail(this.id).then(res=>{
			let [err,{data}] = res
			if(data.code==1){
				this.detail = data.data
			}else{
				uni.showToast({
					title:data.msg,
					icon:"none"
				})
				uni.navigateBack()
			}
		})
	},
	methods: {
		phone(){
			uni.makePhoneCall({
				phoneNumber:'18950387392'
			})
		}
	}
}
